import { Backdrop, Button, ButtonGroup, CircularProgress, Snackbar, Alert } from '@mui/material';
import { ShowUpAnimated } from 'components/animated/showUp';
import React from 'react';
import { connect } from 'react-redux';
import { Route, Routes } from 'react-router-dom';
import { TasksService } from './tasks.service';
import MoveToInboxIcon from '@mui/icons-material/MoveToInbox';
import ForwardToInboxIcon from '@mui/icons-material/ForwardToInbox';
import './style.scss';
import MESSAGES_ACTIONS from 'db/messages/actions';

const TasksView = React.lazy(() => import('./tasks.view'));
const MessagesView = React.lazy(() => import('./message.view'));

class TasksModule extends React.Component {
  /**
   * @type {TasksService}
   */
  #service = undefined;
  constructor(props) {
    super(props);
    this.state = {
      myTasks: true,
      otherTasks: [],
      httpProgress: false,
      taskUpdated: false,
      httpFailed: false,
      httpErrorMessage: '',
      canSelect: true,
      tasks: [],
      messages: props.messages
    };
    this.#service = new TasksService(props.token);
  }

  componentDidMount() {
    this.getTasks();
  }

  componentWillUnmount() {
    this.setState({
      canSelect: true
    });
  }

  getTasks = () => {
    this.setState(
      {
        httpProgress: true
      },
      () => {
        this.#service
          .getAll()
          .then((response) => {
            const tasks = response.data.filter((i) => i.Status < 4);
            const myTasks = [];
            const otherTasks = [];
            for (const item of tasks) {
              if (item.Creator._id === this.props.user.UserId) otherTasks.push(item);
              else myTasks.push(item);
            }

            this.setState({
              tasks: myTasks,
              otherTasks
            });
          })
          .finally(() => this.setState({ httpProgress: false }));
      }
    );
  };

  dispatchTask = ({ id, newState }) => {
    const task = this.state.tasks.find((i) => i._id === id);
    if (task.Status !== newState) {
      this.setState(
        {
          httpProgress: true
        },
        () => {
          this.#service.updateTask(id, newState).then((r) => {
            if (r.status === 1) {
              this.setState(
                {
                  taskUpdated: true,
                  httpFailed: false,
                  httpErrorMessage: ''
                },
                this.getTasks
              );

              setTimeout(() => {
                this.setState({
                  taskUpdated: false,
                  httpFailed: false,
                  httpErrorMessage: ''
                });
              }, 5000);
            } else {
              console.log('else');
              this.setState(
                {
                  httpProgress: false,
                  taskUpdated: true,
                  httpFailed: true,
                  httpErrorMessage: 'خطا در ارسال اطلاعات. کد ' + r.data?.status
                },
                () => {
                  console.log('set state');
                }
              );
              setTimeout(() => {
                this.setState({
                  taskUpdated: false
                });
              }, 5000);
            }
          });
        }
      );
    }
    return false;
  };

  deleteTask = (id) => {
    this.setState(
      {
        httpProgress: true
      },
      () => {
        this.#service.deletTask(id).then((r) => {
          if (r.status === 1) {
            this.setState(
              {
                taskUpdated: true,
                httpFailed: false,
                httpErrorMessage: ''
              },
              this.getTasks
            );

            setTimeout(() => {
              this.setState({
                taskUpdated: false,
                httpFailed: false,
                httpErrorMessage: ''
              });
            }, 5000);
          } else {
            console.log('else');
            this.setState(
              {
                httpProgress: false,
                taskUpdated: true,
                httpFailed: true,
                httpErrorMessage: 'خطا در انجام عملیات. کد ' + r.data?.status
              },
              () => {
                console.log('set state');
              }
            );
            setTimeout(() => {
              this.setState({
                taskUpdated: false
              });
            }, 5000);
          }
        });
      }
    );
    return false;
  };
  render() {
    return (
      <>
        <Backdrop
          open={this.state.httpProgress}
          sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}>
          <CircularProgress />
        </Backdrop>
        <Snackbar
          open={this.state.taskUpdated}
          anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}>
          <Alert severity={this.state.httpFailed ? 'error' : 'success'} sx={{ width: '100%' }}>
            {this.state.httpFailed ? this.state.httpErrorMessage : 'با موفقیت انجام شد'}
          </Alert>
        </Snackbar>
        {this.state.canSelect && (
          <div className="row">
            <div className="col-12 mb-3">
              <ButtonGroup variant="text" color="secondary" className="w-100">
                <Button
                  onClick={() => {
                    this.props.redirect('./');
                    this.setState({
                      myTasks: true
                    });
                  }}
                  variant={this.state.myTasks ? 'contained' : 'text'}
                  className="w-50">
                  تسک های من <MoveToInboxIcon className="me-3" />
                </Button>
                <Button
                  onClick={() => {
                    this.props.redirect('./others');
                    this.setState({
                      myTasks: false
                    });
                  }}
                  variant={!this.state.myTasks ? 'contained' : 'text'}
                  className="w-50">
                  تسک هایی که به بقیه سپردم <ForwardToInboxIcon className="me-3" />
                </Button>
              </ButtonGroup>
            </div>
          </div>
        )}
        <Routes>
          <Route
            path={'/'}
            element={
              <React.Suspense fallback={<>Loading...</>}>
                <ShowUpAnimated>
                  <TasksView
                    dispatchTask={this.dispatchTask}
                    tasks={this.state.tasks}
                    myTasks={this.state.myTasks}
                  />
                </ShowUpAnimated>
              </React.Suspense>
            }
          />
          <Route
            path={'/others'}
            element={
              <React.Suspense fallback={<>Loading...</>}>
                <ShowUpAnimated>
                  <TasksView deleteTask={this.deleteTask} tasks={this.state.otherTasks} />
                </ShowUpAnimated>
              </React.Suspense>
            }
          />
          <Route
            path={'/message/:id'}
            element={
              <React.Suspense fallback={<>Loading...</>}>
                <ShowUpAnimated>
                  <MessagesView
                    onActive={() => this.setState({ canSelect: false })}
                    getMessageDetails={(id) => {
                      return this.state.messages.find((i) => i._id === id);
                    }}
                    sendDm={(userID) => {
                      this.props.redirect('../admins/set_msg/' + userID);
                    }}
                    retry={async () => {
                      const response = await this.#service.getAll();
                      if (response.status === 1) {
                        this.setState(
                          {
                            messages: response.data
                          },
                          () => {
                            this.props.dispatch({
                              type: MESSAGES_ACTIONS.SET,
                              payload: response.data
                            });
                          }
                        );
                        return true;
                      }
                      return false;
                    }}
                  />
                </ShowUpAnimated>
              </React.Suspense>
            }
          />
        </Routes>
      </>
    );
  }
}

const mapStateToProps = (state) => state;
export const Tasks = connect(mapStateToProps)(TasksModule);
export default Tasks;
